.massage-gallery {
  display: grid;
  max-width: 1200px;
  grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  grid-auto-rows: 100px;
  grid-gap: 12px;
  padding: 0;
  padding-left: 15px;
  padding-right: 15px;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
  }
}

.massage-gallery__item {
  position: relative;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.massage-gallery__image:hover {
  transform: scale(1.03);
}

.massage-gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.massage-gallery__link {
  display: block;
  text-decoration: none;
  height: 100%;
}

.massage-gallery__link:hover {
  cursor: zoom-in;
}

/* Lightbox */
.massage-lightbox {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  transition: opacity var(--animation-duration) var(--timing-function);
}

.massage-lightbox.is-open {
  opacity: 1;
  pointer-events: initial;
}

.massage-lightbox__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
}

.massage-lightbox__content {
  transform: scale(0.95);
  transition: transform var(--animation-duration) var(--timing-function);
}

.massage-lightbox.is-open .massage-lightbox__content {
  transform: scale(1);
}

.massage-lightbox__image {
  display: block;
  width: auto;
  height: auto;
  max-height: 100vh;
  max-width: 100vw;
}

.massage-lightbox__button {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
  transition: background-color var(--animation-duration) var(--timing-function);
  outline: none;
  background-image: url('../images/icons/cancel-circle.svg');
  background-position: center;
  background-size: 60%;
  background-repeat: no-repeat;
}

.massage-lightbox__button:hover,
.massage-lightbox__button:focus {
  background-color: rgba(0, 0, 0, 0.5);
}
.massage {
  @media screen and (max-width: 767px) {
    padding-bottom: 50px;
  }
  @media screen and (min-width: 768px) {
    padding-bottom: 100px;
  }
}
.massage-container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  @media screen and (max-width: 479px) {
    max-width: 450px;
  }
  @media screen and (min-width: 480px) {
    width: 480px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
  }
  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
}
.massage-text__container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
}
.massage-title {
  @extend %text-throw;
  text-align: center;
  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
  }
}
.massage-info {
  @extend %text-throw;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.66;
  letter-spacing: 0.02em;
  color: $little-text-color;
  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
  }
}
.massage-link {
  @extend %links-button;
  &__container {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
    @media screen and (max-width: 767px) {
      text-align: left;
    }
    @media screen and (min-width: 768px) {
      text-align: center;
    }
  }
}
